import React from 'react';
import {Route, Redirect } from 'react-router-dom';

export const LoginRoute = ({ component: Component, ...rest }) => (
    <Route
    {...rest}
    render={props =>
    localStorage.getItem('user') ? (
        <Redirect
    to={{
    pathname: "/home"
    }}
    />
    ) : (
        <Component {...props} />
    )
    }
    />
    );